body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.progressHdr {
    padding: 2px;
    background-color: rgb(240, 240, 240);
    max-width: 80%;
    margin: auto;
}
.pInline {
    display: inline;
    font-size: 1em;
    margin: 28px;
}
.commentInput {
    width: 32em;
    outline: none;
    resize: none;
    border-radius: 0.3em;
    border-color: darkgray;
}
p {
    font-size: 1.2em;
}
.App {
  text-align: center;
}

.App-header {
  background-color: #337ab7;
  margin: auto; 
  max-width: 80%;
  min-height: 70px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 2.5em;
}

.App-intro {
  font-size: large;
}

.Notification {
  color: rgb(13, 87, 224);
}


